import vue from 'vue' ;
import Router from 'vue-router';
import HelloWorld from "../components/HelloWorld";
import Card from "../components/Card";//
import CardAdd from "../components/CardAdd";//
import CardArray from "../components/CardArray";//
import CardArrayAdd from "../components/CardArrayAdd";//
import CustomList from "../components/CustomList";//
import CustomListAdd from "../components/CustomListAdd";//
import TaroClass from  "../components/TaroClass";//
import TaroClassAdd from  "../components/TaroClassAdd";//
import TaroVedio from  "../components/TaroVedio";//
import TaroVedioAdd from  "../components/TaroVedioAdd"; //
import AdminList from  "../components/AdminList";//
import AdminListAdd from "../components/AdminListAdd";//
import IntroList from "../components/IntroList";//
import IntroListAdd from "../components/IntroListAdd";//
import CardDay from "../components/CardDay";//
vue.use(Router);

export default new Router({
    routes : [
        {
            path : '/Index',
            component : HelloWorld ,
            name : 'Index'
        },
        {
            path : '/Card' ,
            component : Card ,
            name : 'Card'
        },
        {
            path : '/CardAdd/:id' ,
            component : CardAdd ,
            name : 'CardAdd'
        },
        {
            path : '/CardArray' ,
            component : CardArray ,
            name : 'CardArray'
        },
        {
            path : '/CardArrayAdd/:id' ,
            component : CardArrayAdd ,
            name : 'CardArrayAdd'
        },
        {
            path : '/CustomList' ,
            component : CustomList ,
            name : 'CustomList'
        },
        {
            path : '/CustomListAdd' ,
            component : CustomListAdd ,
            name : 'CustomListAdd'
        },
        {
            path : '/TaroClass' ,
            component : TaroClass ,
            name : 'TaroClass'
        },
        {
            path : '/TaroClassAdd' ,
            component : TaroClassAdd ,
            name : 'TaroClassAdd'
        },
        {
            path: '/TaroVedio',
            component: TaroVedio,
            name: 'TaroVedio'
        },
        {
            path: '/TaroVedioAdd/:id',
            component: TaroVedioAdd,
            name: 'TaroVedioAdd'
        },
        {
            path: '/AdminList',
            component: AdminList,
            name: 'AdminList'
        },
        {
            path: '/AdminListAdd',
            component: AdminListAdd,
            name: 'AdminListAdd'
        },
        {
            path: '/IntroList',
            component: IntroList,
            name: 'IntroList'
        },
        {
            path: '/IntroListAdd/:id',
            component: IntroListAdd ,
            name: 'IntroListAdd'
        },
        {
            path: '/CardDay',
            component: CardDay,
            name: 'CardDay'
        }
    ]
})
