<template>
    <div class="base">
        <h1 class="weight-title ">
            <span></span>
            <div class="pro-tag-list">
                <div class="tag pro-tag" name="production-single"><router-link to="/IntroListAdd/0"><a></a></router-link></div>
                <div class="tag pro-tag" name="production-double"></div>
            </div>
        </h1>
        <!--  -->
        <div class="role-list">
            <table>
                <thead>
                <tr>
                    <th>
                        <input type="checkbox" id="user_all"/>
                        <label  class="user-choose" for="user_all"></label>
                    </th>
                    <th class="middle"></th>
                    <th class="short"></th>
                    <th class="long"></th>
                    <th class="long"></th>
                    <th class="long"></th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="( item ) in list ">
                    <td>
                        <input type="checkbox" id="user_1"/>
                        <label class="user-choose" for="user_1"></label>
                    </td>
                    <td>{{ item.title }}</td>
                    <td>{{ item.class_name }}</td>
                    <td>{{ item.tag }}</td>
                    <td>
                        {{ item.content }}
                    </td>
                    <td>
                        <span class="edit">
                                  <a @click="introAdd(item.id)"></a>
                        </span>
                        <span class="delete"><a @click="introDelete(item.id)"></a></span>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
        <div class="page-list">
            <a class="first" href="javascript:void(0);">
                
            </a>
            <a class="last" href="javascript:void(0);">
                
            </a>
            <a href="javascript:void(0);">
                1
            </a>
            <a href="javascript:void(0);">
                2
            </a>
            <a href="javascript:void(0);">
                3
            </a>
            <a href="javascript:void(0);">
                4
            </a>
            <a href="javascript:void(0);">
                5
            </a>
            <a class="next" href="javascript:void(0);">
                
            </a>
            <a class="pre" href="javascript:void(0);">
                
            </a>
        </div>
    </div>
</template>
<script>
    import request from '../assets/js/ajax.js';
    export default {
        name: "IntroList",
        data : function () {
            return {
                list :[]
            }
        },
        methods : {
            getList:function () {
                var _this = this;
                request.get('intro_list','' ,function (data) {
                    console.log(typeof data);
                    if ( data.code == 1 ){
                        _this.list = data.list ;
                    }else{
                        console.log(data.message);
                    }
                })
            },
            introAdd : function ( id ) {
                if (id){
                    this.$router.push({
                        path : `IntroListAdd/${id}`
                    });
                }
            },
            introDelete :function ( id ) {
                let _this = this;
                request.post('intro_list_delete' , { id : id } , function ( data ) {
                      console.log(data);
                      if ( data.code == 1){
                          _this.getList();
                      }
                })
            }
        },
        created() {
            this.getList();
        }
    }
</script>

<style scoped>

</style>
